<template>
  <v-container :style="$vuetify.breakpoint.mdAndUp === true ? 'max-width: 570px;' : 'max-width: 390px; margin-top:20px; display:flex; flex-direction:column; justify-content:space-between;' ">
    <v-row justify="center" :class="$vuetify.breakpoint.mdAndUp === true ? 'mb-n6' : ''">
      <v-spacer></v-spacer>
      <v-col cols="12" class="my-4" style="display: flex; justify-content: center">
        <h1 style="color:#384FFE; text-align: center ;font-size: 2.4rem">CREA UNA CUENTA</h1>
      </v-col>
      <v-spacer></v-spacer>
    </v-row>
    <v-form
      ref="form"
      v-model="valid"
      lazy-validation>
      <v-row>
        <v-col>
          <v-text-field
            v-model="nombre"
            :label="'Nombre Completo'"
            outlined
            color="#384FFE"
            style="height: 50px;"
            :dense = true
            type = "text"
            :rules="[rules.required]"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-text-field
            v-model="matricula"
            :label="'Matrícula'"
            outlined
            color="#384FFE"
            style="height: 50px;"
            :dense = true
            type = "text"
            :rules="[rules.required]"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-text-field v-if="error.length === 0"
            v-model="emailInstitucional"
            :label="'Correo Institucional'"
            outlined
            required
            color="#384FFE"
            style="height: 50px;"
            :dense = true
            type = "text"
            :rules="[rules.required, rules.email]"
          ></v-text-field>
          <v-text-field v-else
                        v-model="emailInstitucional"
                        :label="'Correo Institucional'"
                        outlined
                        required
                        color="#384FFE"
                        style="height: 50px;"
                        :dense = true
                        type = "text"
                        :rules="[rules.required, rules.email, error]"
                        @change="eraseError"
                        @keydown="eraseError"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-text-field
            v-model="emailPersonal"
            :label="'Correo Personal'"
            outlined
            required
            color="#384FFE"
            style="height: 50px;"
            :dense = true
            type = "text"
            :rules="[rules.required, rules.email]"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-text-field
            v-model="password"
            :label="'Contraseña'"
            outlined
            color="#384FFE"
            style="height: 50px;"
            :dense = true
            :type="show1 ? 'text' : 'password'"
            hint="Al menos 6 caracteres"
            :rules ="[rules.required, rules.min]"
            :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="show1 = !show1"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-text-field
            v-model="confirmPassword"
            :label="'Confirmar Contraseña'"
            outlined
            color="#384FFE"
            style="height: 50px;"
            :dense = true
            :type="show2 ? 'text' : 'password'"
            hint="Al menos 6 caracteres"
            :rules ="[rules.required, rules.min,rules.pass]"
            :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="show2 = !show2"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row class="mt-n8 mb-n8">
        <v-col style="display: flex; justify-content: center">
          <v-checkbox
            label="Acepto términos y condiciones"
            color="#384FFE"
            :rules="[v => !!v || 'You must agree to continue!']"
            required
          ></v-checkbox>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-btn @click="validating" :style="$vuetify.breakpoint.mdAndUp === true ? 'width: 100%; background: #384FFE; color: white; border-radius: 6px; cursor:pointer; font-weight: bold; font-size: 18px; box-shadow: 1px 1px 3px black'
            : 'width: 100%; background: #384FFE; color: white; border-radius: 6px; cursor:pointer; font-weight: bold; font-size:18px; box-shadow: 1px 1px 3px black'"
                 class="py-1" :disabled="!valid" >Registrarse</v-btn>
        </v-col>
      </v-row>
    </v-form>
    </v-container>
</template>

<script>
import firebase from 'firebase/compat'
export default {
  name: 'Register',
  data () {
    return {
      valid: true,
      show1: false,
      show2: false,
      nombre: '',
      matricula: '',
      emailPersonal: '',
      password: '',
      confirmPassword: '',
      emailInstitucional: '',
      error: '',
      uid: '',
      rules: {
        required: value => !!value || 'Required.',
        min: v => v.length >= 6 || 'Al menos 6 caracteres',
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'Invalid e-mail.'
        },
        pass: value => this.password === value || 'Contraseñas diferentes'
      }
    }
  },
  methods: {
    async createUser (email, password) {
      try {
        await firebase.auth().createUserWithEmailAndPassword(email.toUpperCase(), password).then(() => {
          this.login(email, password)
        })
      } catch (e) {
        this.error = e.message
        if (this.error === 'Firebase: The email address is already in use by another account. (auth/email-already-in-use).') {
          this.error = 'Email ya esta en uso por otra cuenta. Por favor ingresa otro Email Institucional'
        }
      }
    },
    async login (email, password) {
      await firebase.auth().signInWithEmailAndPassword(email.toUpperCase(), password).then(() => {
        this.uid = firebase.auth().currentUser.uid
        this.createFirestoreDocForNewUser(this.uid)
      })
    },
    async createFirestoreDocForNewUser (id) {
      await firebase.firestore().collection('users').doc(id).set({
        Nombre_Completo: this.nombre,
        Matricula: this.matricula,
        Email_Personal: this.emailPersonal
      }).then(() => {
        this.$router.push('/Inicio')
      })
    },
    validating () {
      const valid = this.$refs.form.validate()
      if (valid) {
        this.createUser(this.emailInstitucional, this.password)
      }
    },
    eraseError () {
      this.error = ''
    }
  }
}
</script>

